import React, {
    useEffect,
    Fragment,
    useCallback,
    useMemo,
    useState,
    useRef,
    forwardRef,
    useImperativeHandle
} from 'react';
import clsx from 'clsx';
import {
    edsWarning,
    addProps,
    useIsomorphicLayoutEffect
} from '@element/react-common';
import { edsRandomId } from '@element/shared';
import { createPopper } from '@popperjs/core';
import { TooltipDefaultProps, TooltipPropTypes } from './generated/propTypes';
import TooltipText from './TooltipText';

const Tooltip = forwardRef((props, externalRef) => {
    const {
        children,
        className,
        containerType,
        hoisted,
        id,
        onClick,
        portalContainer,
        position,
        text,
        tooltipProps,
        triggerProps,
        ...customProps
    } = props;

    const triggerRef = useRef();
    const tooltipRef = useRef();
    const popperRef = useRef();

    useImperativeHandle(externalRef, () => tooltipRef.current, []);

    const [idActual] = useState(() => id || edsRandomId());

    const handleClick = useCallback(() => {
        onClick?.();
    }, [onClick]);

    const handleKeyPress = useCallback(
        e => {
            if (e.key === 'Enter') onClick?.();
        },
        [onClick]
    );

    useIsomorphicLayoutEffect(() => {
        const modifiers = [
            {
                name: 'flip',
                options: {
                    enabled: true,
                    padding: { bottom: 32, top: 32 }
                }
            }
        ];
        if (!hoisted) {
            modifiers.push({
                name: 'offset',
                options: {
                    offset: [0, 12]
                }
            });
        } else {
            modifiers.push({
                name: 'offset',
                options: {
                    offset: [0, 6]
                }
            });
        }
        // HACK: workaround for jsdom issue when running in a unit test
        if (triggerRef.current && tooltipRef.current) {
            popperRef.current = createPopper(
                triggerRef.current,
                tooltipRef.current,
                {
                    placement: position,
                    modifiers
                }
            );
        }
        return () => {
            popperRef.current?.destroy();
        };
    }, [position, hoisted]);

    const showTooltip = useCallback(() => {
        tooltipRef.current?.setAttribute('data-show', '');
        popperRef.current?.setOptions(options => ({
            ...options,
            modifiers: [
                ...options.modifiers,
                { name: 'eventListeners', enabled: true }
            ]
        }));

        popperRef.current?.update();
    }, []);

    const hideTooltip = useCallback(() => {
        tooltipRef.current?.removeAttribute('data-show');
        popperRef.current?.setOptions(options => ({
            ...options,
            modifiers: [
                ...options.modifiers,
                { name: 'eventListeners', enabled: false }
            ]
        }));
    }, []);

    const isReactFragment = useCallback(() => {
        const el = children;
        if (el.type) {
            return el.type === Fragment;
        }
        return el === Fragment;
    }, [children]);

    useEffect(() => {
        const validationWarning = `Tooltips should only include short text for additional context and should not exceed three lines visually. (Toolitip ID ${idActual}).`;
        text && text.length > 100 && edsWarning(validationWarning);
    }, [text, idActual]);

    const ChildrenRendered = useMemo(
        // eslint-disable-next-line react/no-unstable-nested-components
        () => () => {
            if (!children) return null;
            if (typeof children === 'string' || isReactFragment(children))
                return <span aria-describedby={idActual}>{children}</span>;

            return addProps(children, { 'aria-describedby': idActual });
        },
        [children, idActual, isReactFragment]
    );

    const clickProps = onClick
        ? {
              tabIndex: 0,
              role: onClick ? 'button' : 'note',
              onKeyPress: handleKeyPress,
              onClick: handleClick
          }
        : {};

    return (
        <span
            className={clsx(
                'lmnt lmnt-tooltip',
                containerType === 'block'
                    ? 'lmnt-tooltip--block'
                    : 'lmnt-tooltip--inline'
            )}
            {...clickProps}
            {...customProps}
        >
            <span
                className={clsx(
                    'lmnt-tooltip__content',
                    containerType === 'block'
                        ? 'lmnt-tooltip--block'
                        : 'lmnt-tooltip--inline'
                )}
                {...triggerProps}
                ref={triggerRef}
                onMouseEnter={showTooltip}
                onFocus={showTooltip}
                onMouseLeave={hideTooltip}
                onBlur={hideTooltip}
            >
                <ChildrenRendered />
            </span>
            {text && (
                <TooltipText
                    hoisted={hoisted}
                    portalContainer={portalContainer}
                    role='tooltip'
                    id={idActual}
                    className={className}
                    {...tooltipProps}
                    ref={tooltipRef}
                    text={text}
                />
            )}
        </span>
    );
});

Tooltip.displayName = 'Tooltip';
Tooltip.propTypes = TooltipPropTypes;
Tooltip.defaultProps = TooltipDefaultProps;

export default Tooltip;
