import React, {
    useMemo,
    useRef,
    forwardRef,
    useImperativeHandle,
    useState
} from 'react';
import clsx from 'clsx';
import { Portal } from '@element/react-portal';
import { edsRandomId } from '@element/shared';
import {
    TooltipTextDefaultProps,
    TooltipTextPropTypes
} from './generated/propTypes';

const TooltipText = forwardRef((props, externalRef) => {
    const { className, hoisted, id, portalContainer, text, ...tooltipProps } =
        props;

    const [idActual] = useState(() => id || edsRandomId());
    const tooltipRef = useRef();
    useImperativeHandle(externalRef, () => tooltipRef.current, []);
    const classNames = useMemo(
        () =>
            clsx([
                'lmnt',
                className,
                'lmnt-tooltip__text-content',
                'mdc-typography--caption'
            ]),
        [className]
    );

    return (
        <Portal disabled={!hoisted} container={portalContainer}>
            <div
                role='tooltip'
                id={idActual}
                className={classNames}
                {...tooltipProps}
                ref={tooltipRef}
            >
                {text}
            </div>
        </Portal>
    );
});

TooltipText.displayName = 'TooltipText';
TooltipText.propTypes = TooltipTextPropTypes;
TooltipText.defaultProps = TooltipTextDefaultProps;

export default TooltipText;
